(function () {
	
	'use strict'
	
	angular
		.module('spmiFrontEnd')
		.directive('userNodeList', ['$compile', userNodeList])
		.directive('userNode', ['$compile', userNode])
		.directive('userNodeFormList', ['$compile', userNodeFormList])
		
	function userNodeList ($compile) {
		return {
			restrict: 'E',
			terminal: true,
			replace: true,
			transclude: true,
			scope: {
				nodes: '=ngModel',
				node: '=',
				users: '=',
				nodeController: '@',
				parentIndex: '@'
			},
			controller: '@',
			name: 'nodeController',
			link: function ($scope, $element, $attrs) {

				$scope.template = ''
					+ '<accordion close-others="false">'
						+ '<user-node ng-repeat="item in nodes track by $index" ng-model="item" nodes="nodes" node-index="$index + 1" parent-index="' + $scope.parentIndex + '"  users="users" node-controller="' + $scope.nodeController + '"></user-node>'
					+ '</accordion>'
	
				//console.log($scope.template)
				if (angular.isArray($scope.nodes)) {
					$element.append($scope.template);
				} 
				$compile($element.contents())($scope.$new());
			}, 
		};
	}
	
	function userNode ($compile) {
		return {
			restrict: 'E',
			terminal: true,
			replace: true,
			transclude: true,
			scope: {
				node: '=ngModel',
				nodes: '=',
				users: '=',
				nodeController: '@',
				nodeIndex: '=',
				parentIndex: '@'
			},
			controller: '@',
			name: 'nodeController',
	
			link: function ($scope, $element, $attrs) {

				if ($scope.parentIndex == 'undefined') {
					$scope.parentIndexString = ''
				} else {
					$scope.parentIndexString = $scope.parentIndex + '.'
				}
	
				$scope.parentIndexStringNode = $scope.parentIndexString + $scope.nodeIndex
				
				if (angular.isArray($scope.node.children) && $scope.node.children.length > 0) {
					$scope.template = ''
						+ '<div style="margin-top: 5px;">'
							+ '<accordion-group is-open="node.open">'
								+ '<accordion-heading>'
									+ '<i class="pull-left glyphicon" ng-class="{'
										+ '\'glyphicon-chevron-down\': node.open,'
										+ '\'glyphicon-chevron-right\': !node.open}">'
									+ '</i>&nbsp;'
									+ '{{ parentIndexString }}{{ nodeIndex }}. {{node.header}}'
								+ '</accordion-heading>'
	
								+ '<div class="row">'
									+ '<div class="col-md-7">'
										+ '<h3>{{ parentIndexString }}{{ nodeIndex }}. {{ node.header }}</h3>'
										+ '<h3>Deskripsi</h3>'
										+ '<div class="col-md-12">'
											+ '<p>{{ node.description }}</p>'
										+ '</div><br/>'
									+ '</div>'
	
									+ '<div class="col-md-5">'
										+ '<div class="panel panel-default">'
											+ '<div class="panel-heading clearfix">'
												+ '<div class="panel-title pull-left">'
													+ 'Delegation'
												+ '</div>'
												+ '<div class="panel-title pull-right">'
													+ '<button ng-if="isLeaderLocal" ng-click="delegateNode(node)" class="btn btn-primary btn-xs"><i class="fa fa-plus fa-xs"></i></button>'
												+ '</div>'
											+ '</div>'
											+ '<div class="panel-body">'
												+ '<div class="list-group">'
													+ '<a href="" ng-click="detail(work)" class="list-group-item" ng-repeat="object in node.delegations">'
														+ '<i class="fa" ng-class="{'
															+ '\'fa-user\': object.id !== ' + $scope.isLeader + ','
															+ '\'fa-star\': object.id == ' + $scope.isLeader 
														+ '}"></i>&nbsp;{{ object.name }}'
													+ '</a>'
												+ '</div>'
											+ '</div>'
										+ '</div>'
									+ '</div>'
								+ '</div>'
	
								+ '<div ng-if="node.forms">'
									+ '<node-form-list ng-model="node" node-controller="' + $scope.nodeController + '"></node-form-list>'
								+ '</div>'
								+ '<user-node-list ng-model="node.children" node="node" users="users" node-controller="' + $scope.nodeController + '" parent-index="' + $scope.parentIndexStringNode + '"></user-node-list>'
							+ '</accordion-group>'
						+ '</div>'
	
					$element.append($scope.template);
	
				} else {
	
					$scope.template = ''
						+ '<div style="margin-top: 5px;">'
							+ '<accordion-group>'
								+ '<accordion-heading>'
									+ '<i class="pull-left glyphicon" ng-class="{'
										+ '\'glyphicon-chevron-down\': node.open,'
										+ '\'glyphicon-chevron-right\': !node.open}">'
									+ '</i>&nbsp;'
									+ '{{ parentIndexString }}{{ nodeIndex }}. {{node.header}}'
								+ '</accordion-heading>'
	
								+ '<div class="row">'
									+ '<div class="col-md-7">'
										+ '<h3>{{ parentIndexString }}{{ nodeIndex }}. {{ node.header }}</h3>'
										+ '<h3>Deskripsi</h3>'
										+ '<div class="col-md-12">'
											+ '<p>{{ node.description }}</p>'
										+ '</div><br/>'
									+ '</div>'
	
									+ '<div class="col-md-5">'
										+ '<div class="panel panel-default">'
											+ '<div class="panel-heading clearfix">'
												+ '<div class="panel-title pull-left">'
													+ 'Delegation'
												+ '</div>'
												+ '<div class="panel-title pull-right">'
													+ '<button ng-if="isLeaderLocal" ng-click="delegateNode(node)" class="btn btn-primary btn-xs"><i class="fa fa-plus fa-xs"></i></button>'
												+ '</div>'
											+ '</div>'
											+ '<div class="panel-body">'
												+ '<div class="list-group">'
													+ '<a href="" ng-click="detail(work)" class="list-group-item" ng-repeat="object in node.delegations">'
														+ '<i class="fa" ng-class="{'
															+ '\'fa-user\': object.id !== ' + $scope.isLeader + ','
															+ '\'fa-star\': object.id == ' + $scope.isLeader 
														+ '}"></i>&nbsp;{{ object.name }}'
													+ '</a>'
												+ '</div>'
											+ '</div>'
										+ '</div>'
									+ '</div>'
	
								+ '</div>'
	
	
								+ '<div ng-if="node.forms">'
									+ '<user-node-form-list ng-model="node" node-controller="' + $scope.nodeController + '"></node-form-list>'
								+ '</div>'
								+ '<user-node-list ng-model="node.children" node="node" users="users" node-controller="' + $scope.nodeController + '" parent-index="' + $scope.parentIndexStringNode + '"></user-node-list>'
							+ '</accordion-group>'
						+ '</div>'
	
					$element.append($scope.template);
				}
	
				$compile($element.contents())($scope.$new());
			}
		};
	}
	
	function userNodeFormList ($compile) {
		return {
			restrict: 'E',
			terminal: true,
			replace: true,
			transclude: true,
			scope: {
				node: '=ngModel',
				nodes: '=',
				nodeController: '@',
			},
			controller: '@',
			name: 'nodeController',
			link: function ($scope, $element, $attrs) {
	
				$scope.template = ''
					+ '<div class="row">'
						+ '<div class="col-lg-12">'
							+ '<h3>Formulir</h3>'
							+ '<div class="row">'
								+ '<div class="col-md-6">'									
									+ '<div class="form-group has-feedback">'
										+ '<label class="control-label">Bobot Pekerjaan:&nbsp;</label>'
										+ '{{ node.weight }}'
									+ '</div>'
								+ '</div>'
							+ '</div>'
							+ '<div class="panel panel-default">'
								+ '<div class="panel-heading clearfix">'
									+ '<label class="control-label">Dafar Formulir Penugasan</label>'
								+ '</div>'
								+ '<div class="panel-body">'
									+ '<div class="row">'
										+ '<div class="col-md-12">'
											+ '<div class="table-responsive">'
												+ '<table class="table table-hover">'
													+ '<thead>'
														+ '<tr>'
															+ '<th>#</th>'
															+ '<th><a href="" ng-click="sortField = \'name\'	; reverse = !reverse">Formulir</a></th>'
															+ '<th><a href="" ng-click="sortField = \'uploader\'; reverse = !reverse">Uploader</a></th>'
															+ '<th><a href="" ng-click="sortField = \'date\'	; reverse = !reverse">Date Upload</a></th>'
															+ '<th>Action</th>'
														+ '</tr>'
													+ '</thead>'
													+ '<tbody>'
														+ '<tr ng-repeat="object in node.forms | filter:query |   orderBy:sortField:reverse track by $index">'
															+ '<td>{{ $index + 1 }}</td>'
															+ '<td>{{ object.description }}</td>'
															+ '<td>{{ object.uploads.users.name }}</td>'
															+ '<td>{{ object.uploads.created_at | date:\'dd-MM-yyyy hh:mm\' }}</td>'
															+ '<td>'
																+ '<button ng-if="isDelegate" popover="Detail" popover-trigger="mouseenter" ng-click="detailForm(object.project_form_item_id)" class="btn btn-info btn-xs"><i class="fa fa-search"></i></button>&nbsp;'
															+ '</td>'
														+ '</tr>'
													+ '</tbody>'
												+ '</table>'
											+ '</div>'
											+ '<pre>{{ isDelegate | json }}</pre>'
										+ '</div>'
									+ '</div>'
								+ '</div>'
							+ '</div>'
						+ '</div>'
					+ '</div>	'
	
				$element.append($scope.template);
	
				$compile($element.contents())($scope.$new());
			}
		}
	}
})();
(function () {
	
	'use strict'
	
	angular
		.module('spmiFrontEnd')
		.controller('UserProjectController', UserProjectController)
		.controller('DetailUserProjectController', ['$state', 'project', '$timeout', '$modal', 'CURRENT_USER', 'ProjectService', DetailUserProjectController])
		.controller('NodeUserProjectController', NodeUserProjectController)
		.controller('ModalDetailUserProjectController', ModalDetailUserProjectController)
		.controller('FormDetailUserProjectController', FormDetailUserProjectController)
	
	function showStatus(start, ended, status) {
			var now = new Date();
			
			start = new Date(start);
			ended = new Date(ended);
			
			if (start > now && status == 1) {
				return {
					code: 1,
					text : 'Preparation',
				}
			}
			
			if (start < now && ended > now && status == 1) {
				return { 
					code: 2,
					text: 'On Progress',
				}
			}
			
			if (ended < now && status == 1) {
				return {
					code: 3,
					text: 'Waiting for Scoring',
				};
			}
			
			if (status == 2) {
				return {
					code: 4,
					text: 'Complete',
				};
			}
			
			if (status == 3) {
				return {
					code: 5,
					text: 'Terminated',
				};
			}
		}
	
	function UserProjectController($state, projects, ProjectService) {
		var vm = this;
		vm.projects = projects;
	
		vm.detail = function (request) {
			$state.go('main.user.project.detail', {projectId: request})
		}
		
		vm.showStatus = showStatus;
		
		
		return vm;
	
	}
	
	function DetailUserProjectController($state, project, $timeout, $modal, ProjectService) {
		//console.log(project);
	
		var vm = this;
		vm.input = project;
	
		vm.status = {}
		vm.project_id = {}
		
		vm.input.start = new Date(vm.input.date_start);
		vm.input.ended = new Date(vm.input.date_ended);
		
		switch (showStatus(vm.input.start, vm.input.ended, vm.input.status).code) {
			case 1: vm.phase = '2'; break;
			case 2: vm.phase = '4'; break;
			case 3: vm.phase = '6'; break;
			case 4: vm.phase = '6'; break;
			case 5: vm.phase = '6'; break;
		}
		
		
		
		var recursiveChecking = function(node) {
			for (var i = 0 ; i < node.length ; i++) {
				if (node[i].children.length > 0) {
					recursiveChecking(node[i].children)
				} else {
					for(var j = 0 ; j < node[i].forms.length ; j++) {
						if(node[i].forms[j].uploads) {
							var time = new Date(node[i].forms[j].uploads.created_at)
							time.addHours(7)
							
							node[i].forms[j].uploads.created_at = time
						}
					}
					
					for(var j = 0 ; j < node[i].delegations.length ; j++) {
						if(node[i].delegations[j].id == ProjectService.userId) {
							node[i].isDelegate = true
							break
						}
					}
				}
			}
		}
		
		recursiveChecking(vm.input.projects)
		
		
		
		vm.back = function() {
			$state.go('main.user.project');
		}
	
		return vm;
	}
	
	
	function NodeUserProjectController($scope, $state, $modal, ProjectService, CURRENT_USER) {
		
		console.log($scope.users); //users only triggerd once
		
		if ( typeof $scope.users !== "undefined" ) {
			for ( var i = 0; i < $scope.users.length; i++ ) {
				if ( $scope.users[i].leader == true ) {
					$scope.isLeader = $scope.users[i].id;
					break;
				}
			}
		}

		//check if current user is leader
		//console.log($scope.isLeader);
		//console.log(CURRENT_USER.id);
		if ($scope.isLeader === CURRENT_USER.id) {
			$scope.isLeaderLocal = true ;
		} else {
			 $scope.isLeaderLocal = false;
		}
	
		
		if(typeof $scope.node !== "undefined") {
			if (typeof $scope.node.delegations !== "undefined") {
				var counter = 0;
				for ( var i = 0; i < $scope.node.delegations.length; i++ ){
					if( CURRENT_USER.id === $scope.node.delegations[i].id ) {
						$scope.isDelegate = true;
						break;
					}
					counter++;
				}
				
				if ( counter === $scope.node.delegations.length ) {
					$scope.isDelegate = false;
				}
			}
		}
		
		$scope.inheritDelegation = function(nodes, delegations) {
			for (var i = 0; i < nodes.length; i++) {
				nodes[i].delegations = [];
				for (var j = 0; j < delegations.length; j++) {
					nodes[i].delegations.push(delegations[j]);
				}
				
				$scope.inheritDelegation(nodes[i].children, delegations);
			}
		}
	
	
		$scope.delegateNode = function(node) {
			var projectId = node.id; //get project node id to send after delegations
			
			var modalInstance = $modal.open({
				animate: true,
				templateUrl: 'app/admin/user/views/modal.html',
				controller: 'ModalDetailUserProjectController as vm',
				size: 'lg',
				resolve: {
					users: function() {
						return $scope.users;
					},
					delegations: function () {
						return node.delegations
					}
				}
			});

			modalInstance.result.then(function (result) {
				var data = {
					project_id: projectId,
					delegations: result.users,
					inherit: result.inherit,
				}
				
				ProjectService.delegate(data).then(function(data) {
					node.delegations = result.users;
					if (result.inherit == true) {
						$scope.inheritDelegation(node.children, result.users);
					}
					alert('Project Ini berhasil didelegasikan')
				}, function() {})
				
			}, function () {})
		
		}
		
		$scope.detailForm = function(formId){
			$state.go('main.user.project.detail.form', {formId: formId})
		}
	}
	
	
	
	function ModalDetailUserProjectController($timeout, $modalInstance, users, delegations) {
		
		var vm = this;
		vm.useInherit = true	//configuration for allows inherit delegation
		vm.leader = {}			//leader for this project
		vm.users = [] 			//all users that colaborate on this project
		vm.delegations = [] 	//users that already delegate before on this project node
		vm.input = {}			//variable for return from this modal
		
		//console.log(delegations);
		
		//check inheritance configuration is undefined or defined
		if (vm.inherit) {
			vm.inherit = true;
		} else {
			vm.inherit = false;
		}
		
		//remove leader form users and delegations so he/she cannot be undelegetae
		for (var i = 0; i < users.length; i++ ) {
			if ( users[i].leader == true ) {
				vm.leader = users[i];		
			} else {
				vm.users.push(users[i]);
			}
		}
		
		for (var i = 0; i < delegations.length; i++ ) {
			if ( delegations[i].id !== vm.leader.id ) {
				vm.delegations.push(delegations[i]);	
			}
		}
		
		
		//remove user check before checking user that already delegate
		for (var i = 0; i < vm.users.length; i++) {
			vm.users[i].check = false;
		}
		
		
		//check users if already delegate
		for (var i = 0 ; i < vm.users.length; i++ ) {
			for ( var j = 0; j < vm.delegations.length; j++ ) {
				if ( vm.users[i].id == vm.delegations[j].id ) {
					vm.users[i].check = true;
				}
			}
		}
		
		
		
		//set default message when popup shows
		vm.selected = vm.delegations.length + ' user selected from ' + vm.users.length
		
	
		vm.checkAll = function() {
			if (vm.checked) {
				for ( var i = 0 ; i < vm.users.length; i++ ) {
					vm.users[i].check = true;
				}
				vm.selected = vm.users.length + ' user selected from ' + vm.users.length
			} else {
				for ( var i = 0 ; i < vm.users.length; i++ ) {
					vm.users[i].check = false;
				}
				vm.selected = 0 + ' user selected from ' + vm.users.length
			}
			
	
			
		}
	
		vm.checkCustom = function() {
			//if all users is selected global check turn true
			var counter = 0;
			for ( var i = 0; i < vm.users.length; i++ ) {
				if ( vm.users[i].check == true ) {
					counter++;
				}
			}
			
			if ( counter == vm.users.length ) {
				vm.checked = true;
			} else {
				vm.checked = false;
			}
			
			vm.selected = counter + ' user selected from ' + vm.users.length
		}
	
		vm.submit = function () {
			vm.input = {}
			vm.input.users = []
			
			vm.input.inherit = vm.inherit;	//return inheritance configuration
			vm.input.users.push(vm.leader); //push leader back to project
			
			//push all checked users on list;
			for ( var i = 0; i < vm.users.length; i++ ) {
				if ( vm.users[i].check == true ) {
					vm.input.users.push(vm.users[i]);
				}
			}	
		
			$modalInstance.close(vm.input)
		}
	
		vm.close = function () {
			$modalInstance.dismiss('cancel');
		}
		
		vm.checkCustom(); //check if all users is delegate for set global check
	
		return vm;
	}
	
	function FormDetailUserProjectController($scope, $state, $timeout, form, project, CURRENT_USER, ProjectService) {
		console.log(form)
		console.log(project)
		
		var vm = this;
		
		
		vm.form = form;
		vm.project = project
		vm.leader = project.leader
		vm.projectFormItemId = null
		
		for (var i = 0 ; i < vm.form.uploads.length ; i++) {
			console.log(vm.form.uploads[i])
			
			if (vm.projectFormItemId === null)
			vm.projectFormItemId = vm.form.uploads[i].project_form_item_id;
			
			var time = new Date(vm.form.uploads[i].created_at)
			time.addHours(7)
			vm.form.uploads[i].created_at = time
		}
		
		vm.sortField = 'created_at';
		vm.reverse = true;
		
		
	
		
		vm.upload = function() {
			
			//vm.input.user_id = CURRENT_USER.id;
			console.log(vm.form.id)
			vm.input.project_form_item_id = vm.form.id
			
			
			
			ProjectService.upload(vm.input).then(function(data) {
				alert('Upload Success');
				vm.form.uploads.push(data);
			}, function() {
				
			})
			
		}
		

		return vm;
	}
})();





