		vm.input.year_start = Number(vm.input.year_start);
		vm.input.year_ended = Number(vm.input.year_ended);
		vm.input.date_start = new Date(vm.input.date_start);
		vm.input.date_ended = new Date(vm.input.date_ended);
	
